const routers = [
    {
        path: '/',
        meta: {
            title: '主页'
        },
        component: (resolve) => require(['../views/main.vue'], resolve)
    },
    {
        path: '/contactUs',
        meta: {
            title: '联系我们'
        },
        component: (resolve) => require(['../views/contact.vue'], resolve)
    },
    {
        path: '/report',
        meta: {
            title: '报表'
        },
        component: (resolve) => require(['../views/report.vue'], resolve),
        //children用于嵌套路由，可嵌套多层。
        children: [{
            path: 'goods',
            component: (resolve) => require(['../views/report/goods.vue'], resolve)
        }, {
            path: "order",
            component: (resolve) => require(['../views/report/order.vue'], resolve)
        }, {
            path: "item",
            component: (resolve) => require(['../views/report/item.vue'], resolve)
        }, {
            path: "rank",
            component: (resolve) => require(['../views/report/rank.vue'], resolve)
        }, {
            path: "procurement",
            component: (resolve) => require(['../views/report/procurement.vue'], resolve)
        }, {
            path: "comprehensiveness",
            component: (resolve) => require(['../views/report/comprehensiveness.vue'], resolve)
        }]
    }, {
        path: '/scope',
        meta: {
            title: '区域'
        },
        component: (resolve) => require(['../views/scope.vue'], resolve),
        //children用于嵌套路由，可嵌套多层。
        children: [{
            path: 'region',
            component: (resolve) => require(['../views/scope/region.vue'], resolve)
        }, {
            path: "government",
            component: (resolve) => require(['../views/scope/government.vue'], resolve)
        }, {
            path: "centralEnterprises",
            component: (resolve) => require(['../views/scope/centralEnterprises.vue'], resolve)
        }]
    }, {
        path: '/reptile',
        meta: {
            title: '拾贝'
        },
        component: (resolve) => require(['../views/reptile.vue'], resolve)
    }
];
export default routers;