<template>
    <div class="content" ref="content">
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name: "content",
        data() {
            return {
                height: ''
            }
        },
        mounted() {
            this.height = `${document.documentElement.clientHeight}`
            window.onresize = function () {
                this.height = `${document.documentElement.clientHeight}`;
            };
        },
        watch: {
            height: function () {
                this.changeFixed(this.height)
            }
        },
        methods: {
                changeFixed(clientHeight) {
                this.$refs.content.style.height = (clientHeight - 295) + 'px';
            }
        }
    }
</script>

<style scoped>
    .content {
        height: 900px;
    }
</style>